.post-twice {
	display: none;
	font-weight: 400;
	font-size: 1rem;
	color: red;
	margin-bottom: 0.8rem;
}

.form {
	// display: none;
	display: block;

	position: absolute;
	z-index: 5;
	left: 38%;
	top: 20%;
	//   transform: translateX(-50%);
	width: 100%;
	max-width: 25.5rem;
	min-width: 23rem;
	font-size: clamp(14px, 2.2vw, 18px);
	padding: 0.5rem;
	background-color: var(--background-form-color);
	border: 0.2rem var(--form-border-color) solid;
	border-radius: 0.4rem;

	margin-right: 1.7rem;

	&__button > p {
		display: none;
		color: red;
		font-size: 1rem;
	}
	&__top {
		display: flex;
		align-items: center;
		justify-content: space-between;
		margin-bottom: 1em;
	}
	&__btn-close {
		&:active {
			background-color: #f2b0b0;
		}
	}
	&__top {
		& h1 {
			font-size: clamp(18px, 2.2vw, 20px);
			margin-bottom: 1em;
		}
	}
	&__block {
		display: flex;
		flex-wrap: wrap;
		align-items: flex-start;
		margin-bottom: 1em;
	}
	&__block-mail {
		width: 100%;
		// margin-left: 1em;
	}
	&__inp-mail {
		border: 0.2rem var(--form-border-color) solid;
		border-radius: 0.4rem;
		padding: clamp(0.2rem, 0.6vw, 0.7rem);
		width: 100%;
		background-color: #fff;
		margin-bottom: 0.2em;
	}
	&__block-inp {
		display: flex;
		align-items: center;
		margin-bottom: 0.4em;
		width: 100%;
	}
	&__block-inp {
		& h4 {
			margin-bottom: 0.5em;
		}
	}
	&__block-inp {
		& label {
			width: 100%;
		}
		& textarea {
			width: 100%;
			height: 10rem;
			resize: none;
			border: 0.2rem var(--form-border-color) solid;
			border-radius: 0.4rem;
			padding: clamp(0.4rem, 1vw, 1rem);
		}
	}
	&__btn-block {
		margin: 2.3rem 0;
	}
}

.validate {
	font-size: 1rem;
	margin-bottom: 0.5em;
}
.validate--textarea-duble-send {
	max-width: 600px;
	color: red;
	margin-bottom: 1rem;
}
// comment block
.comment {
	&__out {
		border-radius: 0.4rem;
		background-color: #ffffff;
	}
	&__message {
		display: flex;
		justify-content: flex-start;
		align-items: center;

		padding: 0.8rem;

		& button {
			&:hover {
				background-color: #f2b0b0;
			}
		}
		&:not(:last-child) {
			margin-bottom: 1em;
		}
	}
	&__avatar {
		display: flex;
		align-items: center;
		justify-content: center;
		text-align: center;
		background-color: #a26c6c;
		width: 5rem;
		height: 5rem;
		border-radius: 50%;
		margin-right: 1.5rem;
	}
	&__img-avatar {
		width: 100%;
	}
	&__block-content {
	}
	&__nik-name {
		margin-bottom: 1rem;
		color: var(--nik-name-red);
		& span {
			margin-left: 0.6rem;
			color: var(--nik-time-opacity);
		}
	}
	&__out-text {
		margin-bottom: 1rem;
	}
	&__btn-del {
		padding: 0.5rem;
	}
}
